/* font-family: 'Indie Flower', cursive;
font-family: 'Lobster', cursive;
font-family: 'Otomanopee One', sans-serif; */

/* GENERAL SECTION */




/* NAVBAR */

.branded{
  font-family: 'Otomanopee One', sans-serif;
padding-left: 50px;

}

.ms-auto{
  font-family: 'Otomanopee One', sans-serif;
  font-size: 1.5rem;
  padding-right: 120px;
  color: white;
  text-decoration: none;
  text-align: right;
}
/* WELCOME SECTION */

.Welcome {
  padding-top: 50px;
  padding-bottom: 50px;
  font-family: 'Otomanopee One', sans-serif;
}
.Welcome h1 {
  padding-top: 60px;
  font-size: 7rem;
  text-align: center;
  overflow-wrap: break-word;
}
.Welcome p {
  font-size: 1.6rem;
  text-align: center;
  padding-top: 140px;
}
.Welcome img {
  max-width: 80%;
  height: auto;
  margin: auto;
  display: flex;
  border-radius: 80%;
}


/* ABOUT SECTION */


.About {
  padding-top: 50px;
  padding-bottom: 30px;
  font-family: 'Otomanopee One', sans-serif;
}
.About h1 {
  padding-top: 40px;
  font-size: 6.2rem;
  text-align: center;

}
.About p {
  font-size: 1.4rem;
  padding-top: 70px;
  display: flex;
  text-align: left;
}
.About img {
  max-width: 80%;
  height: auto;
  margin: auto;
  display: flex;

}


/* PORTFOLIO SECTION  */
#image {
  border-radius: 80%;
  max-width: 40%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 30px;
}

.Carousell h1{
  text-align: center;
  font-size: 2.8rem;
  padding: 50px 0px;
  font-family: 'Otomanopee One', sans-serif;
}
.labels{
  font-family: 'Otomanopee One', sans-serif;
}
.carousel-control-prev-icon {
  background-color: black;
  margin: auto;

}
.carousel-control-next-icon {
  background-color: black;
  margin: auto;

}

/* FOOTER SECTION */


.footer{
  text-align: center;
  padding: 40px 0px 20px;
  font-size: 2rem;
}
.iconz{
  font-size: 100px;
  padding: 10px 10px;
  color: white;
}



@media only screen and (max-width: 1200px) {

  .Welcome {
    padding-top: 50px;
    padding-bottom: 50px;
    font-family: 'Otomanopee One', sans-serif;
  }
  .Welcome h1 {
    padding-top: 60px;
    font-size: 4rem;
    text-align: center;
    overflow-wrap: break-word;
  }
  .Welcome p {
    font-size: 1.6rem;
    text-align: center;
    padding-top: 140px;
  }
  .Welcome img {
    max-width: 80%;
    height: auto;
    margin: auto;
    display: flex;
    border-radius: 80%;
  }

  .About {
    padding-top: 50px;
    padding-bottom: 30px;
    font-family: 'Otomanopee One', sans-serif;
  }
  .About h1 {
    padding-top: 40px;
    font-size: 3.2rem;
    text-align: center;
    display: flex;

  }
  .About p {
    font-size: 1.4rem;
    padding-top: 70px;
    display: flex;
    text-align: left;
  }
  .About img {
    max-width: 80%;
    height: none;
    margin: none;
    padding-top: 170px;
    display: static;

  }

}


@media only screen and (max-width: 775px) {

  .Welcome {
    padding-top: 50px;
    padding-bottom: 50px;
    font-family: 'Otomanopee One', sans-serif;
  }
  .Welcome h1 {
    padding-top: 60px;
    font-size: 4rem;
    text-align: center;
    overflow-wrap: break-word;
  }
  .Welcome p {
    font-size: 1.6rem;
    text-align: center;
    padding-top: 140px;
  }
  .Welcome img {
    max-width: 80%;
    height: none;
    margin: none;
    display: none;
    border-radius: 80%;
  }

  .About {
    padding-top: 50px;
    padding-bottom: 30px;
    font-family: 'Otomanopee One', sans-serif;
  }
  .About h1 {
    padding-top: 40px;
    font-size: 3.2rem;
    text-align: center;
    justify-content: center;
    display: flex;

  }
  .About p {
    font-size: 1.4rem;
    padding-top: 70px;
    display: flex;
    text-align: left;
  }
  .About img {
    max-width: 80%;
    height: none;
    margin: none;
    display: none;

  }

}
